/* MatEl - Eagle Eyes Theme - Dark Mode dengan aksen Biru & Emas */

:root {
  /* Color Palette */
  --color-bg-primary: #0a0e1a;
  --color-bg-secondary: #111827;
  --color-bg-tertiary: #1a2332;
  --color-bg-card: #161d2e;

  /* Accent Colors */
  --color-primary: #3b82f6;
  --color-primary-dark: #2563eb;
  --color-secondary: #f59e0b;
  --color-secondary-light: #fbbf24;

  /* Status Colors */
  --color-success: #10b981;
  --color-success-glow: rgba(16, 185, 129, 0.3);
  --color-danger: #ef4444;
  --color-danger-glow: rgba(239, 68, 68, 0.3);
  --color-warning: #f59e0b;
  --color-info: #3b82f6;

  /* Text Colors */
  --color-text-primary: #f3f4f6;
  --color-text-secondary: #9ca3af;
  --color-text-tertiary: #6b7280;

  /* Border & Shadow */
  --color-border: #1f2937;
  --color-border-light: #374151;

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  --gradient-secondary: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  --gradient-success: linear-gradient(135deg, #10b981 0%, #059669 100%);
  --gradient-danger: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);

  /* Spacing */
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --spacing-2xl: 3rem;

  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;

  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.5);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.6);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.7);

  /* Transitions */
  --transition-fast: 150ms ease-in-out;
  --transition-normal: 300ms ease-in-out;
  --transition-slow: 500ms ease-in-out;
}

/* iOS Style Theme */
.theme-ios {
  --color-bg-primary: #f2f2f7;
  --color-bg-secondary: #ffffff;
  --color-bg-tertiary: #ffffff;
  --color-bg-card: #ffffff;
  --color-primary: #007aff;
  --color-primary-dark: #0056b3;
  --color-secondary: #ff9500;
  --color-text-primary: #000000;
  --color-text-secondary: #3c3c43;
  --color-text-tertiary: #8e8e93;
  --color-border: #c6c6c8;
  --radius-md: 10px;
  --radius-lg: 14px;
  --radius-xl: 20px;
  --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 8px 24px rgba(0, 0, 0, 0.12);
  --gradient-primary: linear-gradient(180deg, #007aff 0%, #0051d9 100%);
}

/* Windows 11 (Fluent) Style Theme */
.theme-windows {
  --color-bg-primary: #202020;
  --color-bg-secondary: #2c2c2c;
  --color-bg-tertiary: #323232;
  --color-bg-card: rgba(45, 45, 45, 0.7);
  --color-primary: #60cdff;
  --color-primary-dark: #0078d4;
  --color-secondary: #ffb900;
  --color-text-primary: #ffffff;
  --color-text-secondary: #cccccc;
  --color-text-tertiary: #999999;
  --color-border: rgba(255, 255, 255, 0.08);
  --radius-md: 4px;
  --radius-lg: 8px;
  --radius-xl: 8px;
  --shadow-md: 0 8px 16px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 16px 32px rgba(0, 0, 0, 0.5);
  --gradient-primary: linear-gradient(to right, #60cdff, #0078d4);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--color-bg-primary);
  color: var(--color-text-primary);
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--color-border-light);
  border-radius: var(--radius-md);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary);
}

/* Cyberpunk Ultra Theme */
.theme-cyberpunk {
  --color-bg-primary: #050505;
  --color-bg-secondary: #0a0a0a;
  --color-bg-tertiary: #111111;
  --color-bg-card: rgba(15, 15, 15, 0.7);
  --color-primary: #00f2ff;
  --color-primary-dark: #0080ff;
  --color-secondary: #ff00ff;
  --color-text-primary: #ffffff;
  --color-text-secondary: #b0b0b0;
  --color-text-tertiary: #707070;
  --color-border: rgba(0, 242, 255, 0.2);
  --radius-md: 0;
  --radius-lg: 0;
  --radius-xl: 0;
  --shadow-md: 0 0 20px rgba(0, 242, 255, 0.1);
  --shadow-lg: 0 0 40px rgba(0, 242, 255, 0.2);
  --gradient-primary: linear-gradient(90deg, #00f2ff, #7d00ff);
  --color-success: #39ff14;
  --color-danger: #ff3131;
}

.theme-cyberpunk * {
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
}

.theme-cyberpunk .monitor-card {
  backdrop-filter: blur(12px) saturate(180%);
  border-left: 4px solid var(--color-primary) !important;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - 15px), calc(100% - 15px) 100%, 0 100%);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275) !important;
}

.theme-cyberpunk .monitor-card:hover {
  transform: scale(1.02) translateY(-5px) !important;
  box-shadow: 0 0 30px rgba(0, 242, 255, 0.3) !important;
  border-color: var(--color-secondary) !important;
}

.theme-cyberpunk button {
  text-transform: uppercase;
  letter-spacing: 2px;
  clip-path: polygon(10% 0, 100% 0, 100% 70%, 90% 100%, 0 100%, 0 30%);
}

/* Animation Keyframes */
@keyframes neonPulse {
  0% {
    box-shadow: 0 0 5px rgba(0, 242, 255, 0.2);
  }

  50% {
    box-shadow: 0 0 20px rgba(0, 242, 255, 0.5);
  }

  100% {
    box-shadow: 0 0 5px rgba(0, 242, 255, 0.2);
  }
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(-2px, 2px);
  }

  40% {
    transform: translate(-2px, -2px);
  }

  60% {
    transform: translate(2px, 2px);
  }

  80% {
    transform: translate(2px, -2px);
  }

  100% {
    transform: translate(0);
  }
}

.cyber-pulse {
  animation: neonPulse 2s infinite;
}

.cyber-glitch:hover {
  animation: glitch 0.3s infinite;
}

/* Quantum Aether - Hyper-Premium HQ Theme */
.theme-quantum {
  --color-bg-primary: #02040a;
  --color-bg-secondary: rgba(13, 17, 23, 0.8);
  --color-bg-tertiary: rgba(22, 27, 34, 0.5);
  --color-bg-card: rgba(30, 41, 59, 0.4);
  --color-primary: #8b5cf6;
  --color-primary-dark: #6d28d9;
  --color-secondary: #ec4899;
  --color-text-primary: #ffffff;
  --color-text-secondary: #cbd5e1;
  --color-text-tertiary: #94a3b8;
  --color-border: rgba(139, 92, 246, 0.3);
  --radius-md: 16px;
  --radius-lg: 24px;
  --radius-xl: 32px;
  --shadow-md: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  --shadow-lg: 0 10px 40px 0 rgba(139, 92, 246, 0.2);
  --gradient-primary: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  --color-success: #10b981;
  --color-danger: #f43f5e;
}

/* HQ Quantum Background Aurora */
.theme-quantum::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 30%, rgba(139, 92, 246, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 50% 50%, rgba(6, 182, 212, 0.1) 0%, transparent 50%);
  z-index: -1;
  pointer-events: none;
  animation: auroraFlow 20s infinite alternate linear;
}

.theme-quantum .monitor-card {
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: var(--color-bg-card);
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1) !important;
  transform-style: preserve-3d;
  perspective: 1000px;
}

.theme-quantum .monitor-card:hover {
  transform: translateY(-10px) rotateX(2deg) rotateY(2deg) !important;
  border-color: rgba(139, 92, 246, 0.6);
  box-shadow: 0 20px 40px rgba(139, 92, 246, 0.25);
}

.theme-quantum button {
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.theme-quantum button:hover {
  transform: scale(1.05);
  filter: brightness(1.2);
  letter-spacing: 1px;
}

/* Staggered reveal for HQ */
.theme-quantum .monitor-card {
  animation: quantumEntrance 0.8s cubic-bezier(0.2, 0.8, 0.2, 1) backwards;
}

@keyframes auroraFlow {
  0% {
    transform: scale(1) translate(0, 0);
  }

  50% {
    transform: scale(1.2) translate(5%, 5%);
  }

  100% {
    transform: scale(1) translate(-5%, 2%);
  }
}

@keyframes quantumEntrance {
  0% {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
    filter: blur(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }
}

/* Floating Animation */
@keyframes floatY {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0px);
  }
}

.float-hq {
  animation: floatY 4s ease-in-out infinite;
}

/* Animation Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Utility Classes */
.fade-in {
  animation: fadeIn var(--transition-normal) ease-out;
}

.slide-in {
  animation: slideIn var(--transition-normal) ease-out;
}

.spin {
  animation: spin 1s linear infinite;
}

/* RESPONSIVE MEDIA QUERIES */
@media (max-width: 768px) {
  html {
    font-size: 14px;
    /* Smaller base font for mobile */
  }

  /* Helper helper class to hide elements on mobile if marked */
  .desktop-only {
    display: none !important;
  }

  /* Improve tap targets on mobile */
  button,
  .nav-item {
    min-height: 44px;
    min-width: 44px;
  }
}